import { Component } from '@angular/core';
import { IonicPage, NavController, Loading, AlertController, LoadingController, NavParams, Events } from 'ionic-angular';

import { RestProvider } from '../../providers/rest/rest';
import { LoginPage } from '../../pages/login/login';

@IonicPage()
@Component({
  selector: 'page-faq-detail',
  templateUrl: 'faq-detail.html',
})
export class FaqDetailPage {

	tituloSubtitulo 	= {titulo : "", subtitulo: ""};
	faq 				= [];
	loading: 			Loading; 	// Variable de tipo Loading para mostrar el ProgressBar cuando la pgina est cargando.

  	constructor(private alertCtrl: AlertController, public events: Events, private loadingCtrl: LoadingController, public restProvider: RestProvider,public navCtrl: NavController, public navParams: NavParams) {
  		this.tituloSubtitulo.titulo = "Preguntas de " + this.capitalizeFirstLetter(this.navParams.get('categoria').toLowerCase());
  		this.showLoading();
		this.getFaqDetail(this.navParams.get('categoria'));
		this.events.publish("user:logged");
  	}

  	/**
	* 	Funcin que pone la primera letra en mayuscula
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/
  	capitalizeFirstLetter(string) {
	    return string.charAt(0).toUpperCase() + string.slice(1);
	}

	/**
	* 	Funcin que abre y cierra los cards
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/
  	expandItem(item){

        this.faq.map((listItem) => {

            if(item == listItem){
                listItem.expanded = !listItem.expanded;
            } else {
                listItem.expanded = false;
            }

            return listItem;

        });

    }

  	/**
	* 	Funcin que obtiene todos las categorias
	* 	de las preguntas frecuentes.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/	
	getFaqDetail(c){
		this.restProvider.getFaqDetail(c).then(data => {
			if(typeof data != "undefined" &&  data['status'] == 1){
				this.faq = data['data'];				
				this.loading.dismiss();
			}else if(data.status == 401){
				this.showError("Atencin!","Se ha perdido la sesin, por favor vuelva a iniciar.");
				this.navCtrl.setRoot(LoginPage);				
			}else{
				this.showError("Atencin!","<p>" + data['message'] + "<br/><br/>[Code: " + data['code'] + "]</p>");
			}			
		}).catch(e => {
			this.loading.dismiss();
			console.log(e);
		});		
	}

  	/**
	* 	Funcin que muestra el ProgressBar cuando alguna accin
	*	se est ejecutando en primer plano.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/ 
	showLoading(cont = 'Cargando informacin...') {
		this.loading = this.loadingCtrl.create({
			content: cont
		});
		this.loading.present();
	}
	
	/**
	* 	Funcin que muestra una alerta con el titulo
	*	y el texto pasado por parmetro.
	*
	* 	@param String Titulo de la alerta.
	* 	@param String Texto de la alerta.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	showError(title,text) {	
		this.loading.dismiss();	
		let alert = this.alertCtrl.create({
			title: title,
			message: text,
			buttons: ['OK']
		});
		alert.present();
	}

}



